.ui-more {
  font-size: 16px;
  font-size: 1rem;
  letter-spacing: 0.04rem;
  text-transform: uppercase;
  color: #319DAC;
  width: max-content;
  transition: color 0.5s;
}
.ui-more:hover {
  color: #4C4D6E;
}
.ui-more:hover svg path {
  stroke: #4C4D6E;
}
.ui-more svg {
  margin-right: 4px;
}
.ui-more svg path {
  transition: stroke 0.5s;
}
.ui-more.right {
  display: block;
  margin-left: auto;
}

.ui-btn {
  display: inline-block;
  vertical-align: middle;
  height: 62px;
  line-height: 62px;
  letter-spacing: 0.64px;
  max-width: 100%;
  position: relative;
  border: none;
  border-radius: 30px;
  padding-left: 32px;
  padding-right: 32px;
  white-space: nowrap;
  text-overflow: ellipsis;
  overflow: hidden;
  background: linear-gradient(180deg, #BD5B9E 0%, #A5417D 100%);
  color: #FFF;
  text-align: center;
  font-size: 18px;
  font-size: 1.125rem;
  font-weight: 500;
  cursor: pointer;
  text-transform: uppercase;
  user-select: none;
}
.ui-btn svg {
  margin-left: 12px;
  width: 14px;
  height: 14px;
  margin-right: 6px;
  transition: all 0.5s;
}
.ui-btn svg path {
  transition: all 0.5s;
}
.ui-btn.ui-btn-small {
  height: 41px;
  line-height: 41px;
  padding-left: 20px;
  padding-right: 20px;
  font-size: 16px;
  font-size: 1rem;
}
.ui-btn.ui-btn-green {
  background: linear-gradient(180deg, #319DAC 0%, #278FAF 100%);
}
.ui-btn.ui-btn-transparent {
  font-size: 16px;
  font-size: 1rem;
  background: transparent;
  color: #319DAC;
  height: unset;
  line-height: unset;
  letter-spacing: 0.64px;
  padding: 0;
  width: max-content;
}
.ui-btn.ui-btn-transparent:hover {
  color: #4C4D6E;
  background: none;
}
.ui-btn.ui-btn-transparent:hover svg {
  transition: all 0.5s;
}
.ui-btn.ui-btn-transparent:hover svg path {
  stroke: #4C4D6E;
}
.ui-btn.ui-btn-transparent svg {
  margin-left: 0;
  margin-right: 4px;
}
.ui-btn.ui-btn-tab {
  height: 68px;
  border-radius: 40px;
  padding-left: 38px;
  padding-right: 38px;
  font-size: 20px;
  font-size: 1.25rem;
  background: #FFF;
  color: #343544;
  letter-spacing: 0.6px;
}
.ui-btn.ui-btn-tab.active {
  background: linear-gradient(180deg, #BD5B9E 0%, #A5417D 100%);
  color: #FFF;
}
.ui-btn.ui-btn-tab:hover {
  background: linear-gradient(180deg, #BD5B9E 0%, #A5417D 100%);
  color: #FFF;
}
.ui-btn:hover {
  background: #4C4D6E;
}

.ui-field {
  position: relative;
  display: inline-block;
}
.ui-field label.error {
  font-size: 12px;
