.wrapper {
  display: flex;
  flex-direction: column;
}

.header {
  position: fixed;
  z-index: 10;
  width: 100%;
  top: 0;
  left: 0;
}
.header-top {
  background-color: #319DAC;
}
.header-top-logo {
  width: 220px;
  height: 45px;
  margin-top: auto;
  margin-bottom: auto;
}
.header-top-wrapper {
  display: flex;
  align-items: center;
  height: 80px;
}
.header-top-contact {
  display: flex;
  align-items: center;
  margin-left: auto;
  margin-right: 40px;
  font-size: 18px;
  font-size: 1.125rem;
  letter-spacing: 0.045rem;
}
.header-top-contact .address {
  display: flex;
  align-items: center;
  gap: 12px;
  color: #fff;
  margin-right: 46px;
}
.header-top-contact .phone {
  display: flex;
  align-items: center;
  gap: 20px;
  margin-right: 20px;
}
.header-top-contact .phone a {
  display: inherit;
  color: #fff;
}
.header-top-contact .phone a.viber, .header-top-contact .phone a.tel {
  width: 32px;
  height: 32px;
}
.header-top-contact .socials {
  display: flex;
  align-items: center;
}
.header-top-contact .socials a {
  width: 32px;
  height: 32px;
  display: inherit;
}
.header-bottom {
  background-color: #BD5B9E;
}
.header-bottom .header-nav {
  display: flex;
  align-items: center;
  gap: 60px;
  color: #fff;
  height: 60px;
}
.header-bottom .header-nav-search {
  margin-left: auto;
  cursor: pointer;
  position: relative;
}
.header-bottom .header-nav-search .search-icon {
  width: 26px;
  height: 26px;
}
.header-bottom .header-nav .search-wrapper {
  overflow: hidden;
  visibility: hidden;
  transform: translateY(-10px);
  display: flex;
  align-items: center;
  gap: 10px;
  position: absolute;
  right: 0;
  padding: 12px 20px;
  background-color: #FFF;
  border-radius: 12px;
  box-shadow: 0px 6px 30px 0px rgba(43, 47, 137, 0.15);
  transition: all 0.5s;
}
.header-bottom .header-nav .search-wrapper.open {
  visibility: visible;
  transform: translateY(0);
}
.header-bottom .header-nav .ui-field {
  width: max-content;
  position: relative;
}
.header-bottom nav > ul > li:nth-child(2) > ul {
  padding-right: 0;
}
.header-bottom nav > ul > li:nth-child(2) > ul li a {
  border-top-right-radius: 0px !important;
  border-bottom-right-radius: 0px !important;
}
.header-bottom nav > ul:first-child {
  display: flex;
  gap: 60px;
  align-items: center;
}
.header-bottom nav > ul:first-child > li {
  position: relative;
}
.header-bottom nav > ul:first-child > li.active::after {
  height: 8px;
  width: 100%;
  visibility: visible;
  opacity: 1;
}
